import React, { Component } from 'react';

export default class Cell extends Component{
    constructor(props){
        super(props);

        this.state = ({
            isMouseDown: false,
        });
    }

    clickHandle(x,y){
        let newArray = this.props.map.source;
        newArray[x][y] = 0;
        this.props.mapActions(newArray);
    }

    selectHandle(x,y){
        if(this.state.isMouseDown){
            let newArray = this.props.map.source;
            newArray[x][y] = 0;
            this.props.mapActions(newArray);
        }
        console.log('move');
    }

    moveStartHandle(){
        this.setState({isMouseDown: true});
        console.log('start');
    }

    unMoveHandle(){
        this.setState({isMouseDown: false});
        console.log('stop');
    }

    render(){
        let num = this.props.serialNumber;
        let temp = 'cell ';

        temp += this.props.map.mapping[num];

        return (
            <div
                className={temp}
                // onClick={this.clickHandle.bind(this, this.props.x, this.props.y)}
                onMouseDown={this.moveStartHandle.bind(this)}
                onMouseMove={this.selectHandle.bind(this, this.props.x, this.props.y)}
                onMouseUp={this.unMoveHandle.bind(this)}
            >

            </div>
        );
    }
}